<style lang="scss" scoped>
.TODO {
  background: lightcoral;
}
.container {
  --HeaderHeight: 8em;
  --MinHeightVCard: 32em;
  --MaxWidthColumn: 360px;
  --FooterGradientHeight: 2em;
  --RBSgreen: #009788;
  --RBSpurple: purple;
  --DEV: hotpink;
  xbackground: var(--DEV);
}
header {
  // fixed height because they are in 3 different columns
  height: var(--HeaderHeight);
  max-height: var(--HeaderHeight);
}
H4,
H5 {
  // why edit THREE locations in HTML if you can do it with one CSS selector?
  text-align: center;
  padding-top: 1em;
}
H4 {
  text-transform: uppercase;
}
.column {
  // limit column width on wide screen
  max-width: var(--MaxWidthColumn);
}
.column > .v-card {
  // heighest column sets height for others
  height: 100%;
}
.column .v-card__text {
  // why edit THREE locations in HTML if you can do it with one CSS selector?
  min-height: var(--MinHeightVCard);
  // position V-card content:
  display: flex;
  flex-direction: column;
  align-items: space-between;
}
.column__footer_gradient {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: var(--FooterGradientHeight);
  background-image: linear-gradient(
    to right,
    var(--RBSgreen),
    var(--RBSpurple)
  );
}
// 3e kolom groter
.column:nth-of-type(1),
.column:nth-of-type(2) {
  zoom: 1;
  margin: 2em 0;
}
.column:nth-of-type(3) {
  zoom: 1.1;
}
</style>
<template>
  <v-container>
    <v-row class="CSSGrid3Columns" justify="center">
      <!-- Column #1 Header -->
      <v-col class="column">
        <v-card>
          <v-toolbar flat dark color="purple" height="fit-content">
            <v-row>
              <v-col>
                <h4>
                  Your most effective campaign on FB/Insta or LinkedIN starts
                  here
                </h4>
              </v-col>
            </v-row>
          </v-toolbar>
          <v-card-text>
            <v-row>
              <!-- Column 1 Body -->
              <v-col>
                <v-list dense>
                  <v-list-item
                    v-for="(social, index) in socials"
                    :key="'feature' + index"
                    v-staticIMG:24px="social"
                  >
                    <!--  24px is .arg (String) parameter for Directive, overrules the width set in social -->

                    <v-list-item-content>
                      <v-subheader>{{ social.name }}</v-subheader>
                    </v-list-item-content>

                    <v-list-item-icon>
                      <v-icon color="green">mdi-check-bold</v-icon>
                    </v-list-item-icon>
                  </v-list-item>
                </v-list>
                <v-list dense>
                  <v-list-item
                    v-for="(feature, index) in features"
                    :key="'feature' + index"
                  >
                    <v-sheet
                      rounded="circle"
                      color="green"
                      dark
                      width="28"
                      height="28"
                      class="mx-auto text-center pt-1"
                    >
                      {{ index + 1 }}</v-sheet
                    >

                    <v-list-item-content>
                      <v-subheader>{{ feature }}</v-subheader>
                    </v-list-item-content>

                    <v-list-item-icon>
                      <v-icon color="green">mdi-check-bold</v-icon>
                    </v-list-item-icon>
                  </v-list-item>
                </v-list>
                <v-spacer />
              </v-col>
            </v-row>
          </v-card-text>
          <!-- Column #1 Footer -->
          <div class="column__footer_gradient"></div>
        </v-card>
      </v-col>
      <!-- Column #2 Header -->
      <v-col class="column">
        <v-card>
          <v-toolbar flat dark color="purple" height="fit-content">
            <v-row>
              <v-col>
                <h4>Easy Setup</h4>
              </v-col>
            </v-row>
          </v-toolbar>
          <!-- Column #2 Body -->
          <v-card-text>
            <v-row>
              <v-col class="d-flex flex-column mt-3">
                <v-spacer />
                <span class="d-flex">
                  <!-- DE: added dynamic icon path so code runs on every environment -->
                  <v-list-item
                    v-for="(social, index) in socials"
                    :key="'feature' + index"
                    v-staticIMG="social"
                  >
                  </v-list-item>
                </span>
                <v-spacer />
                <center
                  v-html="htmltext_access_promo_text"
                  class="pb-12 mb-12"
                ></center>
                <v-spacer />
              </v-col>
            </v-row>
          </v-card-text>
          <!-- Column #2 Footer -->
          <div class="column__footer_gradient"></div>
        </v-card>
      </v-col>
      <!-- Column #3 Header -->
      <v-col class="column">
        <v-card>
          <v-toolbar flat dark color="purple" height="fit-content">
            <v-row>
              <v-col>
                <h4>Start your 14 day free trial now</h4>
                <h5>
                  after free trial $89 per month or continue with free version
                </h5>
              </v-col>
            </v-row>
          </v-toolbar>
          <!-- Column #3 Body -->
          <v-card-text>
            <v-row>
              <v-col class="d-flex flex-column">
                <v-spacer />
                <center>
                  <v-btn
                    dark
                    color="#009788"
                    class="pa-12"
                    @click="$emit('closeModal')"
                    >Let's go<br /><v-icon large class="ml-4"
                      >mdi-arrow-right</v-icon
                    ></v-btn
                  >
                </center>
                <v-spacer />
                <v-sheet
                  rounded
                  color="#ccc"
                  class="mx-auto text-center py-2 px-4"
                  >No credit card required</v-sheet
                >
              </v-col>
            </v-row>
          </v-card-text>
          <!-- Column #3 Footer -->
          <div class="column__footer_gradient"></div>
        </v-card>
      </v-col>
    </v-row>
    <v-row justify="center">
      <v-col
        v-for="(company, index) in companyLogos"
        :key="'companyLogo' + index"
        v-staticIMG="company"
      >
      </v-col>
    </v-row>
    <div class="TODO">
      Bovenstaande PNG Logos komen van Neville, onder oorspronkelijk design:
    </div>
    <v-row justify="center">
      <v-col>
        <img src="https://i.imgur.com/7Qjrb48.png" width="100%"/>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
const companyLogosDir = "company_logos/";
const companyLogoWidth = "100%";
export default {
  name: "AdAccountsIntroPopup",
  data() {
    return {
      companyLogos: [
        {
          name: "BuddhatoBuddha",
          img: companyLogosDir + "buddhatobuddha.png",
          width: companyLogoWidth,
        },
        {
          name: "ByTerry",
          img: companyLogosDir + "byTerry.png",
          width: companyLogoWidth,
        },
        {
          name: "Carharrt",
          img: companyLogosDir + "carhartt.png",
          width: companyLogoWidth,
        },
        {
          name: "CitizenM",
          img: companyLogosDir + "CitizenM.png",
          width: companyLogoWidth,
        },
        {
          name: "Heineken",
          img: companyLogosDir + "HeinekenLogo.png",
          width: companyLogoWidth,
        },
        {
          name: "Terres des Hommes",
          img: companyLogosDir + "terresdesHommes.png",
          width: companyLogoWidth,
        },
      ],
      socials: [
        {
          name: "Facebook",
          img: "purpleFacebook.svg",
          width: "48px",
        },
        {
          name: "LinkedIn",
          img: "purpleLinkedin.svg",
          width: "64px",
        },
        {
          name: "Instagram",
          img: "purpleInstagram.svg",
          width: "48px",
        },
      ],
      features: [
        "Quick smart overview of all your campaigns",
        "New metrics you can't see in FB/LI",
        "Get real life alerts and notifications",
        "One Click reporting tool",
        "Save a lot of time of your daily work",
        "And many more",
      ],
      htmltext_access_promo_text: `Getting access to these amazing set of features you just have to
            connect your <b>Business manager</b> and you are all set to get more
            magic`,
    };
  },
  computed: {},
  methods: {
    // DE: I wanted to create ONE "component" for the header (only displays a H4,H5)
    // I tried a component first, failed, then went for an inline function
    // couldn't get v- tags in returned string working...
    // and now CSS is not applied to H4; probably a scoped thing
    // header(idx) {
    //   let headers = [
    //     [
    //       "Your most effective campaign on FB/Insta or LinkedIN starts here",
    //       "",
    //     ],
    //     ["a", "b"],
    //     ["3", ""],
    //   ];
    //   return `<h4>${headers[idx][0]}</h4><h5>${headers[idx][1]}</h5>`;
    // },
  },
  mounted() {},

  directives: {
    staticIMG: {
      // adds an IMG tag at *afterbegin* the start of the referenced element
      // WITHOUT creating an addition Vue wrapper element
      // using v-html would create extra elements

      // STATIC assets moeten niet in de bundle...

      inserted(
        el, // element that called Directive
        binding // data binding
      ) {
        // todo: move to UTIL library
        const isLocalHost =
          location.host.startsWith("192") || location.host.startsWith("local");

        let { name, img, width = "24px" } = binding.value;
        let assetsFolder = "../../../";
        if (isLocalHost) {
          // DE: I couldn't get the proper path in 10 minutes, so moved the files to /public/temp_assets
          assetsFolder = `./static/`;
        } else {
          // ../../../images
          console.warn(`Fix svgIconPath() on Production IMG:`, icon);
        }
        width = binding.arg || width; // if :24px="social" defined use that
        console.log(width, img, name, typeof binding.arg, binding.arg);
        el.insertAdjacentHTML(
          "afterbegin",
          `<img width="${width}" src="${assetsFolder + img}" alt="${name}"/>`
        );
      },
    },
  },
};
</script>

